body, h1, h2, h3, h4, h5, h6, ul, ol, li, p, input, option, textarea, dl, dd, dt {
	margin: 0;
	padding: 0;
}

html, body {
	user-select: none;
	background-color: #fff;
	font-size: 12px;
	font-family: "微软雅黑", Arial, Helvetica, sans-serif;
	color: #333;
	width: 100%;
	height: 100%;
}


ul, ol {
	list-style: none;
}

img {
	border: none;
}

a {
	text-decoration: none;
	color: #333;
}

i, em {
	font-style: normal;
}

.float_l {
	float: left;
}

.float_r {
	float: right;
}

.show {
	display: block !important;
}

.hide {
	display: none;
}

.clearfix:after {
	content: "";
	display: block;
	font-size: 0;
	height: 0;
	clear: both;
}


.img {
	width: 100%;
	height: 100%;
	float: left;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none !important;
	margin: 0;
}

input[type="number"] {
	-moz-appearance: textfield;
}

.inner {
	box-sizing: border-box;
	width: 14rem;
	margin: 0 auto;
}

.text_hide {
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 1;
	-webkit-box-orient: vertical;
}



